.textarea {
  width: 100%;
  resize: none;
  font-size: 16px;
  font-family: "gothampro-regular", Arial, sans-serif;
  border: 1px solid #fff;
  box-shadow: inset 0 0 30px 5px rgba(62, 53, 42, 0.11);
  background-color: #c8b29a;
  padding: 24px 24px 24px 38px;
  letter-spacing: 0.200rem;
  text-transform: uppercase;
  transition: border-color .32s ease, box-shadow .42s ease;
}

.textarea::placeholder {
  color: #fff;
}

.textarea-field {
  position: relative;
}

.textarea-field .textarea:focus {
  box-shadow: none;
}

.textarea-field:hover:before, .textarea-field:hover:after {
  background-color: #e6e6e6;
}

.textarea-field:hover .textarea {
  border-color: rgba(255, 255, 255, 0.3);
}

.textarea-field:before, .textarea-field:after {
  content: "";
  background-color: rgba(255, 255, 255, 0.3);
  display: block;
  position: absolute;
  transition: background-color .32s ease;
}

.textarea-field:before {
  bottom: -8px;
  left: 0;
  width: calc(100% + 8px);
  height: 1px;
}

.textarea-field:after {
  right: -8px;
  height: calc(100% + 8px);
  width: 1px;
  top: -1px;
}

