.order {
	padding: 100px 0 95px 0;
	@include image(bg-order);
	@include cover;
	text-align: center;
	@include r(768) {
		padding: 60px 0; }
	@include r(667) {
		padding: 40px 0; }
	h3 {
		margin-bottom: 70px;
		text-transform: uppercase;
		color: $main;
		font-size: $text-lg; //36px
		@include font-family($gotham-reg);
		@include r(768) {
			margin-bottom: 30px; } }
	form {
		max-width: 770px;
		margin: 0 auto;
		font-size: 0;
		@include r(860) {
			max-width: 90%; }
		.grid {
			&__item {
				@include r(860) {
					width: 100%;
					padding-right: 0;
					margin-bottom: 15px; }
				@include r(640) {
					width: 50%;
					padding-right: 15px; }
				@include r(568) {
					width: 100%;
					padding-right: 0; } }
			&-double-spacing {
				@include r(860) {
					margin-right: 0; }
				@include r(640) {
					margin-right: -15px; }
				@include r(568) {
					margin-right: 0; } } } }
	.textarea-field {
		margin: 15px auto 57px;
		@include r(860) {
			margin: 0 auto 30px; } } }


// features
.features {
	background-image: url("../img/bg-features.png");
	@include cover;
	position: relative;
	@include r(992) {
		background-position: left center; }
	.image-left {
		overflow: hidden;
		height: 890px;
		margin-left: 40px;
		@include r(1024) {
			margin-left: 20px; }
		@include r(992) {
			display: none; }
		img {
			max-width: 100%;
			height: auto; } }
	&__right {
		float: right;
		margin-right: -20px;
		font-size: 0;
		display: table;
		max-width: 670px;
		position: relative;
		@include r(992) {
			max-width: 100%;
			width: 100%;
			display: inline-block;
			vertical-align: top;
			float: none; }
		.list-features {
			width: 50%;
			padding: 0 20px;
			max-width: 320px;
			display: inline-block;
			vertical-align: top;
			@include r(992) {
				max-width: 100%; } }
		.title-shaped {
			top: 0;
			right: 0; } } }
.image {
	&-left {
		float: left; } }


// lorem
.loremin {
	position: absolute;
	display: inline-block;
	width: 840px;
	top: 10px;
	right: -250px;
	vertical-align: top;
	@include r(1166) {
		width: 680px;
		top: 70px;
		right: -250px; }
	@include r(992) {
		top: 108px;
		position: relative;
		right: 0;
		left: 0;
		width: 100%;
		height: 815px; }
	@include r(768) {
		height: 605px; } }
.lorem2 {
	width: 100%;
	padding-bottom: 100%;
	z-index: 1;
	background-size: cover;
	background-repeat: no-repeat;
	@include r(1166) {
		width: 70%; }
	@include r(992) {
		width: 100%;
		padding-bottom: 0;
		height: 100%; } }
.lorem {
	overflow: hidden;
	@include clr;
	background-image: url("../img/bg-dark.png");
	@include cover;
	padding: 110px 0 105px 0;
	.inner {
		@include r(768) {
			padding: 0 30px; }
		&__in {
			position: relative;
			@include r(768) {
				padding: 0 30px; } } }
	&__desc {
		float: left;
		padding-left: 45px;
		max-width: 675px;
		@include r(768) {
			padding-left: 0;
			float: none;
			max-width: 100%; }
		.title-shaped {
			margin-left: 61px; }
		h3 {
			font-size: 60px;
			letter-spacing: 0.020rem;
			color: $white;
			line-height: 1;
			@include font-family($gotham-reg);
			margin: 58px auto 41px;
			text-transform: uppercase; }
		p.price {
			@include font-family($gotham-reg);
			font-size: 119.7px;
			color: $brown2;
			line-height: 1;
			letter-spacing: -0.050rem;
			strong {
				@include font-family($gotham-md);
				display: inline-block;
				font-size: 80px; } } }
	&__about {
		margin-top: 85px;
		.btn-group {
			margin-top: 70px;
			.btn {
				@include r(992) {
					max-width: 231px; } } }
		p {
			@include font-family($gotham-light);
			font-size: 14px;
			line-height: 2.13;
			color: $gray; }
		.title-icon {
			font-size: 14px;
			color: $gray;
			text-transform: uppercase;
			position: relative;
			padding-bottom: 29px;
			@include font-family($gotham-light);
			margin-left: 50px;
			margin-bottom: 30px;
			&:after {
				content: "";
				display: block;
				position: absolute;
				left: 0;
				top: 100%;
				background-color: rgba($coffee, 0.45);
				height: 1px;
				width: 60px; }
			i {
				position: absolute;
				top: -12px;
				left: -50px; } } } }
